import useLocales from '../../hooks/useLocales';

import MainBanner from '../../components/wrappers/MainBanner';
import EventSearchComponent from '../../components/form/EventSearchComponent';

import { useMediaQuery } from '@mui/material';

export default function BannerSection() {
    const { t } = useLocales();
    const isMobile = useMediaQuery('(max-width:400px)');

    return (
        <MainBanner>
            <div className="flex flex-col gap-8 justify-center items-center">
                <div className="max-w-full text-center">
                    <h1>
                        Seu lugar para {isMobile && <br />} <span className="text-yellow-400">comprar</span> e&nbsp;
                        <span className="text-yellow-400">vender</span>
                        <br />
                        <span className="text-yellow-400">ingressos&nbsp;</span>
                        de fã para fã
                    </h1>
                </div>
                <div className={`mb-10 -mt-4 ${isMobile ? 'text-sm' : 'text-lg/[19px]'}`}>{t('description.secure_payment')}</div>
                <div className="max-w-xl w-full">
                    <EventSearchComponent />
                </div>
            </div>
        </MainBanner>
    );
}
