import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Icon } from '@iconify/react';
import { EVENTS_LOCATION } from '../../_mocks';
import useLocales from '../../hooks/useLocales';
import TicketImageCard from '../../components/cards/TicketImageCard';

import { useMediaQuery } from '@mui/material';

export default function LocationEventSection() {
    const { t } = useLocales();
    const isMobile = useMediaQuery('(max-width:500px)');
    const navigate = useNavigate();

    const [reivewIndex, setReviewIndex] = useState(0);
    const nextReview = () => {
        if (reivewIndex < EVENTS_LOCATION.length) {
            setReviewIndex(reivewIndex + 3);
        } else setReviewIndex(0);
    };
    const prevReview = () => {
        if (reivewIndex > 0) {
            setReviewIndex(reivewIndex - 3);
        } else setReviewIndex(0);
    }; 
    
    const handleAllEvents = () => {
        navigate(`/event/search_event`);
    };
    return (
        <div className="w-full">
            <div className="container">
                <div className="text-center flex flex-col gap-4 py-4 md:py-8">
                    <h1>
                        <span className="text-yellow-400">Eventos</span> por Localização
                    </h1>
                    <p>
                        Busque eventos pelos lugares mais
                        <br /> populares
                    </p>
                </div>
                <div className="flex flex-row gap-2 items-center mb-4">
                    <a href={`#item-location-${reivewIndex}`} onClick={prevReview}>
                        <Icon icon="ic:baseline-arrow-back-ios-new" width={isMobile ? 25 : 48} className="cursor-pointer" />
                    </a>
                    <div className="carousel flex-1">
                        {EVENTS_LOCATION.map((event, index) => (
                            <div key={index} id={`item-location-${index}`} onClick={handleAllEvents} className={`carousel-item w-[${isMobile ? 200 : 337}px] p-4 aspect-3/4`}>
                                <TicketImageCard card={{ ...event, image: `url(${event.image})` }}>
                                    <p className="text-3xl/[32px] font-medium">{event.name}</p>
                                </TicketImageCard>
                            </div>
                        ))}
                    </div>
                    <a href={`#item-location-${reivewIndex}`} onClick={nextReview}>
                        <Icon icon="ic:baseline-arrow-forward-ios" width={isMobile ? 25 : 48} className="cursor-pointer" />
                    </a>
                </div>
                <div onClick={handleAllEvents} className="flex justify-center mb-16">
                    <button className="btn btn-outline btn-primary py-4 px-8 text-sm/[19px] capitalize border-2 rounded-xl h-auto min-h-0">
                        {t('action.see_all_cities')}
                    </button>
                </div>
            </div>
        </div>
    );
}
