import { Icon } from '@iconify/react';
import { useState } from 'react';
import styled from 'styled-components';
import { CUSTOMERS } from '../../_mocks';
import CustomerReviewCard from '../../components/cards/CustomerReviewCard';

import { useMediaQuery } from '@mui/material';

export default function CustomerReviews() {
    const [reivewIndex, setReviewIndex] = useState(0);
    const isMobile = useMediaQuery('(max-width:500px)');

    const nextReview = () => {
        if (reivewIndex < CUSTOMERS.length) {
            setReviewIndex(reivewIndex + 1);
        } else setReviewIndex(0);
    };
    const prevReview = () => {
        if (reivewIndex > 0) {
            setReviewIndex(reivewIndex - 1);
        } else setReviewIndex(0);
    };

    const RootStyle = styled.div`
    width: '100%';
    background: calc(100vw - ${isMobile ? 130 : 280}px) -${isMobile ? 12 : 85}% / ${isMobile ? 100 : 200}px no-repeat url(/images/clients/1.png),
        calc(100vw - ${isMobile ? 130 : 280}px) ${isMobile ? 36 : 30}% / ${isMobile ? 100 : 200}px no-repeat url(/images/clients/2.png),
        calc(100vw - ${isMobile ? 130 : 280}px) ${isMobile ? 86 : 145}% / ${isMobile ? 100 : 200}px no-repeat url(/images/clients/3.png),
        calc(100vw - 520px) -30% / ${isMobile ? 100 : 200}px no-repeat url(/images/clients/4.png),
        calc(100vw - 520px) 85% / ${isMobile ? 100 : 200}px no-repeat url(/images/clients/5.png), #e9ecf4;
    `;

    return (
        <RootStyle>
            <div className="container py-8 md:py-16 text-black">
                <div className={`w-full flex flex-col justify-center items-center md:pr-[220px]  ${isMobile && 'px-3'}`}>
                    <div className="flex gap-4 items-center justify-start w-full mb-8">
                        <svg width={isMobile ? 26 : 67} height="64" viewBox="0 0 67 64" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M58.436 4.174C59.543 4.174 60.6047 4.61376 61.3875 5.39654C62.1703 6.17932 62.61 7.24099 62.61 8.348V41.74C62.61 42.847 62.1703 43.9087 61.3875 44.6915C60.6047 45.4743 59.543 45.914 58.436 45.914H48.001C46.705 45.914 45.4268 46.2158 44.2677 46.7953C43.1085 47.3749 42.1002 48.2164 41.3226 49.2532L33.392 59.826L25.4614 49.2532C24.6838 48.2164 23.6755 47.3749 22.5163 46.7953C21.3572 46.2158 20.079 45.914 18.783 45.914H8.348C7.24099 45.914 6.17932 45.4743 5.39654 44.6915C4.61376 43.9087 4.174 42.847 4.174 41.74V8.348C4.174 7.24099 4.61376 6.17932 5.39654 5.39654C6.17932 4.61376 7.24099 4.174 8.348 4.174H58.436ZM8.348 0C6.13398 0 4.01063 0.879519 2.44507 2.44507C0.879519 4.01063 0 6.13398 0 8.348L0 41.74C0 43.954 0.879519 46.0774 2.44507 47.6429C4.01063 49.2085 6.13398 50.088 8.348 50.088H18.783C19.431 50.088 20.0701 50.2389 20.6497 50.5287C21.2293 50.8185 21.7334 51.2392 22.1222 51.7576L30.0528 62.3304C30.4416 62.8488 30.9458 63.2695 31.5253 63.5593C32.1049 63.8491 32.744 64 33.392 64C34.04 64 34.6791 63.8491 35.2587 63.5593C35.8383 63.2695 36.3424 62.8488 36.7312 62.3304L44.6618 51.7576C45.0506 51.2392 45.5548 50.8185 46.1343 50.5287C46.7139 50.2389 47.353 50.088 48.001 50.088H58.436C60.6501 50.088 62.7734 49.2085 64.339 47.6429C65.9045 46.0774 66.784 43.954 66.784 41.74V8.348C66.784 6.13398 65.9045 4.01063 64.339 2.44507C62.7734 0.879519 60.6501 0 58.436 0L8.348 0Z"
                                fill="#F2B705"
                            />
                            <path
                                d="M29.4939 19.8684C28.6808 18.6022 27.4787 17.6341 26.0683 17.1096C24.6579 16.5852 23.1154 16.5326 21.6726 16.9599C20.2297 17.3872 18.9646 18.2712 18.0672 19.4792C17.1699 20.6871 16.6888 22.1537 16.6964 23.6584C16.6971 24.9045 17.0325 26.1275 17.6674 27.1997C18.3024 28.2719 19.2136 29.1539 20.3059 29.7536C21.3982 30.3532 22.6315 30.6486 23.8769 30.6087C25.1224 30.5689 26.3343 30.1953 27.386 29.5271C26.8392 31.1507 25.8207 32.8829 24.1428 34.6193C23.8218 34.9514 23.6458 35.3975 23.6536 35.8593C23.6615 36.3211 23.8524 36.761 24.1845 37.082C24.5166 37.403 24.9627 37.579 25.4245 37.5712C25.8863 37.5633 26.3262 37.3724 26.6472 37.0403C32.8498 30.6123 32.0442 23.625 29.4939 19.8768V19.8684ZM46.1899 19.8684C45.3768 18.6022 44.1747 17.6341 42.7643 17.1096C41.3539 16.5852 39.8114 16.5326 38.3686 16.9599C36.9257 17.3872 35.6606 18.2712 34.7632 19.4792C33.8659 20.6871 33.3848 22.1537 33.3924 23.6584C33.3931 24.9045 33.7285 26.1275 34.3634 27.1997C34.9984 28.2719 35.9096 29.1539 37.0019 29.7536C38.0942 30.3532 39.3275 30.6486 40.5729 30.6087C41.8184 30.5689 43.0303 30.1953 44.082 29.5271C43.5352 31.1507 42.5168 32.8829 40.8388 34.6193C40.5178 34.9514 40.3418 35.3975 40.3496 35.8593C40.3575 36.3211 40.5484 36.761 40.8805 37.082C41.2126 37.403 41.6587 37.579 42.1205 37.5712C42.5824 37.5633 43.0222 37.3724 43.3432 37.0403C49.5458 30.6123 48.7402 23.625 46.1899 19.8768V19.8684Z"
                                fill="#1C1C1C"
                            />
                        </svg>
                        <h1>
                            Quem usou e <span className="text-yellow-400">curtiu</span>!
                        </h1>
                    </div>
                    <div className="flex flex-row gap-2 items-center overflow-hidden w-full">
                        <a href={`#customer-review-${reivewIndex}`} onClick={prevReview}>
                            <Icon
                                icon="ic:baseline-arrow-back-ios-new"
                                className="cursor-pointer text-3xl"
                                color="#99A1A3"
                            />
                        </a>
                        <div className="flex-1 carousel">
                            {CUSTOMERS.map((customer, index) => (
                                <div
                                    id={`customer-review-${index}`}
                                    className="p-2 md:px-4 min-w-full lg:min-w-[50%]"
                                    key={index}
                                >
                                    <CustomerReviewCard customer={customer} />
                                </div>
                            ))}
                        </div>
                        <a href={`#customer-review-${reivewIndex}`} onClick={nextReview}>
                            <Icon
                                icon="ic:baseline-arrow-forward-ios"
                                className="cursor-pointer text-3xl"
                                color="#99A1A3"
                            />
                        </a>
                    </div>
                </div>
            </div>
        </RootStyle>
    );
}
