import { fTypeDate } from '../../utils/formatTime';

export default function NextEventCardHome({ event, handleEvent = () => {} }) {
    return (
        <div
            className="flex px-6 p-4  justify-between bg-base-200 rounded-xl cursor-pointer hover:bg-secondary/50"
            onClick={handleEvent}
        >
            <div className="flex items-stretch justify-between flex-col gap-3">
                <label className="text-xl capitalize">{event?.name}</label>
                <p className="text-stone-400 text-sm/[11px]">
                    {fTypeDate(event?.dateTime)}&nbsp;&nbsp;&#x2022;&nbsp;&nbsp;{event?.place}
                </p>
            </div>
            <div className="flex flex-col justify-center">
                <div className="rounded-full items-center flex gap-2 px-3 py-1 bg-[#5A5A5A] text-yellow-400">
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M14.8215 1.37708L14.8218 1.3766C14.9876 1.12906 15.2388 1 15.485 1H18.148C18.6187 1 19 1.38128 19 1.852V18.148C19 18.6187 18.6187 19 18.148 19H15.485C15.2388 19 14.9876 18.8709 14.8218 18.6234L14.8215 18.6229C13.7818 17.0731 12.0105 16.049 10 16.049C7.98951 16.049 6.21822 17.0731 5.17854 18.6229L5.17822 18.6234C5.01219 18.8712 4.76191 19 4.515 19H1.852C1.38128 19 1 18.6187 1 18.148V1.852C1 1.38128 1.38128 1 1.852 1H4.515C4.76122 1 5.01237 1.12906 5.17822 1.3766L5.17854 1.37708C6.21815 2.92685 7.9884 3.951 10 3.951C12.0116 3.951 13.7818 2.92685 14.8215 1.37708Z"
                            stroke="#F2B705"
                            strokeWidth="2"
                        />
                    </svg>
                    <span>{event?.tickets?.length}</span>
                </div>
            </div>
        </div>
    );
}
