import Page from '../components/Page';
import {
    BannerSection,
    CategoryListSection,
    CustomerReviews,
    LocationEventSection,
    NextEventSection,
    WhyUseSection
} from '../sections/main';

export default function Main() {
    return (
        <Page title="Fanticket">
            <div className="flex flex-col">
                <BannerSection />
                <div className="h-24" />
                <WhyUseSection />
                <div className="h-24" />
                <NextEventSection />
            </div>
            <div className="flex flex-col">
                <CategoryListSection />
                <div className="h-6" />
                <LocationEventSection />
                <div className="h-6" />
                <CustomerReviews />
            </div>
        </Page>
    );
}
