import { Icon } from '@iconify/react';
import TicketCard from '../../components/cards/TicketCard';

export default function WhyUseSection() {
    return (
        <div className="container flex-col flex gap-12 px-4">
            <div className="flex gap-4 justify-center items-center">
                <div>
                    <img
                        src="/images/background/ticket-logo-icon.png"
                        alt="ticketlogo"
                        className='logo'
                    />
                </div>

                <h1>
                    <span className="text-yellow-400">3 motivos</span>&nbsp;para usar
                </h1>
            </div>
            <div className="container flex flex-col gap-4 md:gap-8 md:flex-row mb-10 justify-center">
                <div className="w-full">
                    <TicketCard>
                        <div className="rounded-xl border-white border-2 border p-3 mb-4">
                            <Icon width={26} icon="ri:shield-check-line" />
                        </div>
                        <p className="text-center">Receba seu dinheiro com segurança de compradores verificados</p>
                    </TicketCard>
                </div>
                <div className="w-full">
                    <TicketCard>
                        <div className="rounded-xl border-white border-2 border p-3 mb-4">
                            <Icon width={26} icon="uil:money-withdraw" />
                        </div>
                        <p className="text-center">
                            O comprador confia pois o pagamento é liberado após a realização do evento
                        </p>
                    </TicketCard>
                </div>
                <div className="w-full">
                    <TicketCard>
                        <div className="rounded-xl border-white border-2 border p-3 mb-4">
                            <Icon width={26} icon="mdi:user-multiple" />
                        </div>
                        <p className="text-center">Segurança e conveniência para vendedores e compradores</p>
                    </TicketCard>
                </div>
            </div>
        </div>
    );
}
