import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { useSelector } from '../../redux/store';
import useLocales from '../../hooks/useLocales';
import NextEventCardHome from '../../components/cards/NextEventCardHome';

import { useMediaQuery } from '@mui/material';

const NextEventRightBackground = styled.div`
    background: 0% 20% / 40% no-repeat url(/images/background/foto-banner.png),
        100% 20% / 40% no-repeat url(/images/background/next-event-right-vector.png),
        linear-gradient(341.2deg, #393939 0.3%, #151515 32.12%);
`;

export default function NextEventSection() {
    const { t } = useLocales();
    const { events } = useSelector((state) => state.event);
    const isMobile = useMediaQuery('(max-width:450px)');

    const navigate = useNavigate();

    const onChangeEvent = (eventId) => {
        navigate(`/event/${eventId}/basic`);
    };

    const handleAllEvents = () => {
        navigate(`/event/search_event`);
    };

    return (
        <NextEventRightBackground>
            <div className="container">
                <div className={`ml-[41%] py-10`}>
                    <div className="flex flex-col gap-2">
                        <h1>
                            Próximos <span className="text-yellow-400">Eventos</span>
                        </h1>
                        <p className={`text-[#99A1A3] ${!isMobile && 'text-sm'}`}>
                            Eventos relevantes cadastrados pela
                            <br /> plataforma
                        </p>
                    </div>
                    <div className={`grid grid-cols-12 gap-4 mt-${isMobile ? 5 : 10}`}>
                        {events?.slice(0, 8).map((event, index) => (
                            <div className="col-span-12 lg:col-span-6" key={index}>
                                <NextEventCardHome event={event} handleEvent={() => onChangeEvent(event?._id)} />
                            </div>
                        ))}
                    </div>
                    <div className={`w-full flex justify-end ${!isMobile && 'mt-5'}`}>
                        <button className="btn btn-outline btn-primary py-4 px-8 text-sm/[19px] capitalize border-2 rounded-xl h-auto min-h-0" onClick={handleAllEvents} >
                            {t('action.list_all_events')}
                        </button>
                    </div>
                </div>
            </div>
        </NextEventRightBackground>
    );
}
