import { Icon } from '@iconify/react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { CATEGORY_LIST } from '../../_mocks';

const RootStyle = styled.div`
    background: 0% 20% / 30% no-repeat url(/images/background/category-bg.png), #2c2c2c;
`;

const CategoryCard = ({ category, isAddition = false }) => {
    const navigate = useNavigate();
    
    const handleAllEvents = () => {
        navigate(`/event/search_event`);
    };

    return (
        <div
            className="col-span-12 sm:col-span-6 md:col-span-4 flex items-center rounded-xl bg-no-repeat h-28 md:h-32 lg:h-40 gap-2 px-2 sm:px-4 md:p-6 backdrop-grayscale overflow-hidden hover:scale-105 transition-all cursor-pointer"
            style={{
                ...(!isAddition && {
                    backgroundImage: `url(${category.image})`,
                    backgroundSize: 'cover',
                    backgroundColor: '#0c0c0c',
                    backgroundPosition: 'center'
                })
            }}
            onClick={handleAllEvents}
        >
            <div
                className={`bg-black h-full w-full rounded-xl absolute -z-10 -ml-2 sm:-ml-4 md:-ml-6 ${isAddition ? 'border-2 border-white bg-transparent' : 'opacity-30'
                    }`}
            />
            {category.icon}
            <label className="text-2xl cursor-pointer">{category.name}</label>
        </div>
    );
};
export default function CategoryListSection() {
    return (
        <RootStyle>
            <div className="w-full py-4 sm:py-6 md:py-8 container px-2">
                <div className="text-center flex flex-col gap-4 pt-4 md:pt-8">
                    <h1>
                        Categorias de <span className="text-yellow-400">Eventos</span>
                    </h1>
                    <span>Busque eventos por categoria</span>
                </div>
                <div className="h-8" />
                <div className="grid grid-cols-12 w-full gap-2 sm:gap-4 md:gap-8">
                    {CATEGORY_LIST.map((category, index) => (
                        <CategoryCard category={category} key={index} />
                    ))}
                    <CategoryCard
                        category={{
                            icon: (
                                <div className="p-2 rounded-lg">
                                    <Icon icon="material-symbols:add-circle-outline" width={36} />
                                </div>
                            ),
                            name: 'Outras categorias'
                        }}
                        isAddition
                        index="addon"
                    />
                </div>
            </div>
        </RootStyle>
    );
}
